.wellness-section{
    margin-top: 110px;
    margin-left: 290px;
    color: #333;
    margin-bottom: 10%;
}
.wellness-header p{
font-weight: 600;
font-size: 2rem;
}
.wellness-text p{
    font-size: 1.3rem;
    line-height: 27px;
}
.wellness-sub-header {
    font-size: 1.5rem;
}
.wellness-link{
    font-size: 1.2rem;
}
.counselling:hover{
    border: 1px solid #f43f5e;
}
.wellness-centre img{
    width: 45vw;
    height: 50vh;
}
.ambulance-no{animation: shad 1.5s linear infinite;}
@keyframes shad {
    0% {
        opacity: 1;
        scale: 1.1;

    }

    50% {
        opacity: 0.2;
    }

    100% {
        opacity: 1;
    }
}
.wellness-center:hover{border: 1px solid #f43f5e;}
@media (max-width:1024px) {
    .wellness-section{
        margin-left: 0px;
        margin-top: 150px;
    }
    
    .wellness-section p{
        font-size: 1.5rem;
        line-height: 30px;
    }
    .wellness-section-s1{
        flex-wrap: wrap;
    }
    .wellness-counselling-ce p{
        font-size: 1.5rem;
    }
    .wellness-centre {
        margin: auto;
    }
    .wellness-centre img{
        width: 100vw;
        
    }
}
@media (max-width:1700px) {
    .wellness-text p{
        font-size: 1.5rem;
        font-weight: 30px;
    }
}