.main-sports{
    margin-left: 290px;
    margin-bottom: 10%;
}
img{
    width: 300px;
    height: 300px;
    object-fit: cover;
    object-position: center;
}
.sports-photos{
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

@media screen and (max-width:1024px){
    .main-sports{
        margin-left: 10px;
        margin-right: 10px;
        margin-top: 130px;
    }
    .sports-photos{
        justify-content: center;
    }
    .sports-photos img{
        width: 100vw;
        height: 400px;
    }
    /* img{
        width: 500px;
        height: 300px;
    } */
}

