.home {
    color: #4c5157;
    margin-top: 100px;
    margin-bottom: 15%;
}
.fs{
    font-size: var(--bodytext);
    line-height: 27px;
}
.App{
   overflow-y:auto;
    text-align: center;
    color: #4c5157;
    width: 80vw;
}
.marqueee{
    width: 40vw;
    height: 55vh;
    overflow: hidden;
    position: relative;
    background: #fefefe;
    color: #333;
}
.charter:hover{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.marqueee p{
                position: absolute;
                width: 100%;
                height: 100%;
                margin: 0;
                line-height: 50px;
                padding-left: 10px;
                -moz-transform: translateY(100%);
                -webkit-transform: translateY(100%);
                transform: translateY(100%);
                -moz-animation: scroll-left 1ms linear infinite;
                -webkit-animation: scroll-left 1ms linear infinite;
                animation: scroll-left 40s linear infinite;
}
@-moz-keyframes scroll-left {
    0% {
        -moz-transform: translateY(50%);
    }
    100% {
        -moz-transform: translateY(-100%);
    }
}

@-webkit-keyframes scroll-left {
    0% {
        -webkit-transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(-100%);
    }
}

@keyframes scroll-left {
    0% {
        -moz-transform: translateY(100%);
        -webkit-transform: translateY(100%);
        transform: translateY(100%);
    }
    100% {
        -moz-transform: translateY(-100%);
        -webkit-transform: translateY(-100%);
        transform: translateY(-100%);
    }
}

.home {
    margin-left: 290px;

    
}
ul li::before{
    color: red;
}

.title {
    margin-bottom: 50px;
}

.image_wrapper {
    height: 55vh;
    width: 70vw;
    margin: 0px 10px;
    position: relative;
    top: 0;
    bottom: 0;
}

.header {
    position: absolute;
    top: 80%;
    left: 40%;
    z-index: 99;
    color: #fff;
    
    opacity: 0.7;
}

.image_wrapper img {
    width: 70vw;
    height: 55vh;
    object-fit: cover;
    object-position: center;
}

.footer {
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.about {
    height: 250px;
    width: 400px;
    padding: 20px 25px;
    color: #4c5157;
}



.location {

   
    border: 1px solid #4c5157;
    padding: 20px 25px;
    color: #4c5157;
}
.about-page{
   width: 35vw;
}
.charter{
    width: 40vw;
    height: 60vh;
}



.location:hover {
    box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
}

.address {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.about-page p{
    line-height: 24px;
}
.div-main{
    margin-top: 50px;
    gap: 150px;
}
.image-div img{
    width: 600px;
    height: 300px;
}

@media (max-width:1024px) {
    .home {
        margin-top: 150px;
        margin-left: 0px;
        
        
        display: flex;
        flex-direction: column;
        gap: 20px;
        align-items: center;
        margin-bottom: 50%;
       
    }
.wrapper-about{
    margin: 20px;
}
.about-page {
    width: 100vw;
}
.charter{
    width: 100vw;
}
.marqueee{
    width: 100vw;
}
.about-page p{
    font-size: 1.1rem;
    line-height: 25px;
}
    hr {
        display: none;
    }

    .header {
        top: 80%;
        left: 25%;
    }

    .App {
        width: 100vw;
        margin-left: 0px;
        margin-right: 0px;
    }

    .footer {
        gap: 10px;
        width: 100px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        
    }

    .location-sw {
        display: flex;
        flex-wrap: wrap;
        width: auto;
    }

    .location {

        height: max-content;
    }

    .about {
        line-height: 30px;
        height: max-content;
    }

    .contact-info {
        padding-bottom: 30px;
        padding-bottom: 40%;
    }

    .about-homepage {
        line-height: 30px;
        margin: 0px 10px;
    }
    .charter{
        margin-bottom: 50px;
        
    }
    .charter p{
        font-size: 1.1rem;
    }
}

@media only screen and (min-width: 1700px) {
    .sub-header{
        font-size: 2rem;
    }
    .App{
        width: 90vw;
       
    }
    .fs{
        font-size:1.6rem;
        line-height: 30px;
    }
    .about-page p{
        font-size: 1.6rem;
        line-height: 35px;
    }
    .home {
        margin-left: 290px;
    }
    .image-div img{
        width: 50vw;
        height: 40vh;
    }
    .div-main{
        margin-top: 150px;
        gap: 200px;
    }
    .marqueee{
        height: 45vh;
    }
}