.admin-office{
    margin-left: 290px;
    margin-right: 20px;
    margin-top: 120px;
    margin-bottom: 10%;
}
.about-office{
    margin: 30px;
}
.name{
    font-size:1.2rem ;
    font-weight: 600;
}
.des{
    font-size: 1rem;
    font-weight: 600;
    
    color: #343a40;
}
.admin-header p{
    font-size: 1.5rem;
    font-weight: 600;
}
.admin-office-main{
    gap: 20px;
}
.officers{
    padding-left: 40px;
}
.officer-header{
    font-size: 1.3rem;
    font-weight: 600;
}
.hostels-unit{
    font-size: 1.3rem;
    font-weight: 600;
}

@media screen and (max-width:1024px){
    .admin-office{
        margin-left: 10px;
        margin-right: 10px;
        margin-top: 130px;
        
    }
    .admin-header{
        font-size: 3rem;
        font-weight: 600;
    }
    .admin-office h4{
    font-size: 18px;
    }
    .admin-office p{
        font-size: 19px;
    }
    .admin-office  h2{
        font-size: 12px;
    }
    .admin-office-main{
       margin: auto;
    }
}
@media screen and (min-width:1700px){
    .admin-office-main{
        gap:100px;
        justify-content: space-around;
        margin: auto;
    }
    .officer-header{
        font-size: 1.8rem;
    }
    .hostels-unit{
        font-size: 1.7rem;
    }
    .officers{
        padding-left: 300px;
    }
    .name{
        font-size: 1.8rem;
    }
    .des{
        font-size: 1.5rem;
    }

}