.form {
    margin-top: 200px;
    margin-left: 290px;
    margin-right: 10px;
    margin-bottom: 10px;
    width: 80vw;
    color: #313132;
}
.show{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 0;
    bottom: 0;
    font-size: 1.3rem;
}
.googleforms{
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 0;
    bottom: 0;
    font-size: 1.3rem;
}

.table {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    top: 0;
    bottom: 0;
    font-size: 1.3rem;
    
}
.table-1{
    width: 100%;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}
.table-2{
position: absolute;
top: 28%;
left: 50%;
bottom: 20%;
width: 30%;

}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 70%;
}
tr{
    background-color: #f9fafb;
}

td {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 10px;
}

tr:nth-child(even) {
    background-color: #f3f4f6;
}

@media screen and (max-width:1024px) {
    .form{
        margin-left: 10px;
        margin-right: 10px;
        width: 100vw;
    }
    .main-form{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .show{
        width: 100vw;
        font-size: 1.3rem;
    }
    .table{
       width: 100vw;
       font-size: 1.3rem;
    }
    .googleforms{
        width: 100vw;
        font-size: 1.3rem;
    }
}
